import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { IssueReceiveGoodsComponent } from './issue-receive-goods.component';
import { FormsModule } from '@angular/forms';
import { Routes, RouterModule } from '@angular/router';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { AddComponent } from './add/add.component';
import { DetailComponent } from './detail/detail.component';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { DetailSendoComponent } from './detail-sendo/detail-sendo.component';
import { BillType3SendoModule } from 'src/app/sales/components/bill-type/bill-type-3/sendo/bill-type1-sendo.module';
import { EditComponent } from './edit/edit.component';

const routes: Routes = [
  {
    path: "",
    component: IssueReceiveGoodsComponent
  },
  {
    path: "add",
    component: AddComponent
  },
  {
    path: "edit",
    component: EditComponent
  },
  {
    path: "detail",
    component: DetailComponent
  },
  {
    path: "detail-sendo",
    component: DetailSendoComponent
  }
]

@NgModule({
  declarations: [
    IssueReceiveGoodsComponent, AddComponent, DetailComponent, DetailSendoComponent, EditComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule,
    BillType3SendoModule  ]
})
export class IssueReceiveGoodsModule { }
